import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HeroAppComponent } from './hero-app.component';
import { FormsModule } from '@angular/forms';
import { HeroDetailComponent } from './hero-detail/hero-detail.component';
import { HeroService } from './hero.service';
import { LogService } from './log.service';
import { HeroLiComponent } from './hero-li/hero-li.component';
import { HeroListComponent } from './hero-list/hero-list.component';
import { CheckingChangesPipe } from './checking-changes.pipe';
import { MarkCollapsedDirective } from './mark-collapsed.directive';

@NgModule({
  imports: [
    CommonModule,
    FormsModule
  ],
  declarations: [
    HeroAppComponent,
    HeroDetailComponent,
    HeroLiComponent,
    HeroListComponent,
    CheckingChangesPipe,
    MarkCollapsedDirective
  ],
  exports: [HeroAppComponent],
  providers: [HeroService, LogService]
})
export class HeroAppModule { }



