import { Pipe, PipeTransform } from '@angular/core';
import { LogService } from './log.service';

@Pipe({
  name: 'checkingChanges',
  pure: false
})
export class CheckingChangesPipe implements PipeTransform {
  constructor(private logService: LogService) {}
  transform(entity: any, prop: string, args?: any): any {
    let log: any;
    if (prop) {
      log = {
        constructorOfComponent: entity.constructor.name,
        [prop]: entity[prop]
      };
    } else {
      log = {
        constructorOfComponent: entity.constructor.name,
      };
    }

    this.logService.addLog(log);
    // debugger;
    // console.log(log);
    // console.log(`checking changes on ${entity.constructor.name} with ${prop} as ${JSON.stringify(entity[prop])}`)
    return null;
  }

}



