import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
// import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/map';

import { Hero } from './hero';
import { HeroService } from './hero.service';
import { LogService } from './log.service';
@Component({
  selector: 'app-hero-app',
  template: require('./hero-app.component.html'),
  styles: [require('./hero-app.component.scss')],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeroAppComponent implements OnInit {
  private title: string;
  private myHero: Hero;

  private heroesRxx: BehaviorSubject<Hero[]>;
  private heroRxxMap: Map<any, BehaviorSubject<Hero>>;
  private actionRxx: any;
  private selectedHeroRxx: any;

  constructor(
    private heroService: HeroService,
    private logService: LogService) {
      this.title = 'Tour of Heroes';
    }

  ngOnInit() {
    this.heroesRxx = this.heroService.getHeroesRxx();
    this.heroRxxMap = this.heroService.getHeroRxxMap();
    this.actionRxx = this.heroService.getActionRxx();
    this.selectedHeroRxx = this.heroService.selectedHeroRxx;
  }

  onClick(hero: Hero): void {
    this.myHero = hero;
  }

}



