import { Directive, HostListener, HostBinding } from '@angular/core';

@Directive({
  selector: '[appMarkCollapsed]'
})
export class MarkCollapsedDirective {
  @HostBinding('class.collapsed') private collapseState: boolean = false;

  constructor() { }
  @HostListener('click') toggle() {
    this.collapseState = !this.collapseState;
  }
}



