import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { AppCommonComponent } from './app.common.component';
import { AppCommonRoutingModule } from './app.common-routing.module';
import { HomeComponent } from './home/home.component';
import { HeroAppModule } from './hero-app/hero-app.module';

/**
 * Top-level NgModule "container"
 */
@NgModule({
  declarations: [ AppCommonComponent, HomeComponent ],
  exports: [ AppCommonComponent ],
  imports: [
    CommonModule,
    FormsModule,
    AppCommonRoutingModule,
    HeroAppModule
  ]
})
export class AppCommonModule {

}



