import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';

import { Hero } from '../hero';

@Component({
  selector: 'app-hero-li',
  template: require('./hero-li.component.html'),
  styles: [require('./hero-li.component.scss')],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeroLiComponent implements OnInit {
  @Input() hero: Hero;
  @Input() actionRxx: any;

  constructor() { }

  ngOnInit() {
  }

  selectHero() {
    if ( !this.hero.selected ) {
      this.actionRxx.next({type: 'select', payload: {id: this.hero.id}});
    }
  }

  change(newName) {
    if (newName !== this.hero.name) {
      this.actionRxx.next({
        type: 'change',
        payload: {
          id: this.hero.id,
          newName
        }
      });
    }
  }

  delete() {
    this.actionRxx.next({
      type: 'delete',
      payload: {
        id: this.hero.id
      }
    });
  }
}



